












import Vue from "vue";
import { Component } from "vue-property-decorator";
import { mapActions } from "vuex";

@Component({
  methods: { ...mapActions(["logout", "init"]) }
})
export default class App extends Vue {
  init!: () => void;
  logout!: () => void;

  get loggedIn() {
    return this.$store.state.user.loggedIn;
  }
  created() {
    this.init();
  }
}
